
.two-btnlist-box{
  display:none
}
.two-btnlist{
  display:block;
}
.frontend {
  position: relative;
}
.position-filter {
  position: absolute;
  width: 100%;
  bottom: 0
}
.button-two-box {
  overflow: hidden;
}
.button-two-box .li {
  width: 50%;
  float: left;
}
.button-two-box .li:first-child {
  padding-right: .6rem;
}
.btn-box button {
  margin-bottom: .6rem;
  width: auto;
  padding: .4rem .4rem
}
.padding-all {
  padding: 1.3rem;
}
.btn {
  font-size: 1.7rem;
  border: 0;
  text-align: center;
  padding: .84rem 0;
  border-radius: 4px;
  width: 100%;
}
.btn-default {
  color: #333;
  background-color: #fff;
  border: 1px solid #ccc;
}
.btn-primary {
  color: #fff;
  background-color: #428bca;
}
.btn-success {
  color: #FFF;
  background-color: #5cb85c;
}
.btn-info {
  color: #FFF;
  background-color: #5bc0de;
}
.btn-warning {
  color: #FFF;
  background-color: #eb9316;
}
.btn-danger {
  color: #FFF;
  background-color: #d9534f;
}
.btn-link {
  color: #333;
}



