
.mod-search header {
    width: 100%;
    height: 5.4rem;
    line-height: 5.4rem;
    vertical-align: middle;
    color: #fff;
    z-index: 12;
}
.header-search {
    z-index: 111;
    position: absolute;
    -webkit-box-pack: center;
    -moz-box-pack: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    display: -ms-flexbox;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: rgba(236, 81, 81, 0);
    padding: 0 1rem;
    overflow: hidden;
    height: 5.4rem;
    line-height: 3.4rem;
    width: 100%
}
.mod-search .fixed-color {
    display: none
}
.mod-search .search-left {
    float: left;
    width: 6rem;
    /*padding: 0 1rem 0 0;*/
    padding-top: .6rem;
    padding-bottom: .6rem;
}
.mod-search .search-right {
    float: right;
    padding: 0 0 0 1rem;
}
.mod-search .search-left,
  .mod-search .search-right {
    -ms-flex-pack: center;
        justify-content: center;
}
.mod-search .search-left i {
    font-size: 1rem
}
.mod-search .search-center {
    margin-top: .6rem;
    margin-bottom: .6rem;
    width: 64%;
    float: left;
    -ms-flex: 1;
        flex: 1;
    -webkit-flex:1;
    height: 3.4rem;
    line-height: 3.4rem;
    padding: 0 1rem;
    box-sizing: content-box;
    font-size: 1.4rem;
    border-radius: .4rem;
    background: #fff;
    color: #999;
}
.mod-search .search-center i.icon-search {
    margin-right: .4rem;
}
.mod-search .search-right {
    float: right;
    margin-top: .6rem;
    margin-bottom: .6rem;
    position: relative;
    -ms-flex-pack: center;
        justify-content: center;
}
.mod-search .search-right i {
    -ms-flex: 1;
        flex: 1;
    font-size: 2.2rem;
}
.mod-search .search-right em {
    position: absolute;
    display: block;
    width: .6rem;
    height: .6rem;
    border-radius: 100%;
    background: #ff0000;
    right: .3rem;
    top: .1rem;
    border: 1px solid #fff;
}
header.search-visual,
  header.search-fixed,
  header.search-preview {
    z-index: 11;
    width: 100%;
    background: rgba(236, 81, 81, 0);
}
header.search-visual {
    position: relative;
}
header.search-preview {
    position: absolute;
}
header.search-fixed {
    position: absolute;
}
header.search-fixed .search-mask,
  header.search-visual .search-mask,
  header.search-preview .search-mask {
    position: absolute;
    background-image: linear-gradient( rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.4) 60%, rgba(0, 0, 0, 0) 98%);
    z-index: -1;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
}
header.search-visual .search-center,
  header.search-fixed .search-center {
    background: rgba(255, 255, 255, 0.6);
    color: #fff;
}
  
