
.mod-nav nav {
  background: #fff;
  min-height: 6rem;
}
.mod-nav nav.nav-gap-out {
  padding: .8rem .6rem;
}
.mod-nav nav.nav-gap-in ul li {
  padding: .8rem .4rem;
}
.mod-nav nav.nav-border ul li {
  border-right: 1px solid #f0f3f6;
  border-bottom: 1px solid #f0f3f6;
}
.mod-nav nav ul {
  width: 100%;
  overflow: hidden;
}
.mod-nav nav ul li {
  width: 20%;
  float: left;
  text-align: center;
  position: relative
}
.mod-nav nav ul li a {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.mod-nav nav ul li span {
  font-size: 1.2rem;
  color: #333;
  margin-top: .4rem;
  display: block;
}
.mod-nav nav ul li img {
  width: 80%;
  height: auto;
}



