
.product-list {
  padding: .05rem;
}
.product-list ul {
  overflow: hidden;
}
.product-list ul li {
  width: 50%;
  padding: .1rem;
  padding-bottom: 0;
  float: left;
  position: relative;
}
.product-list ul li a {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2
}
.product-list ul li .product-cart {
  position: absolute;
  right: .7rem;
  bottom: .7rem;
  width: 2.2rem;
  height: 2.2rem;
  line-height: 2.2rem;
  background: #ec5151;
  text-align: center;
  border-radius: 100%;
  color: #fff;
  z-index: 3
}
.product-list ul li .product-cart i {
  font-size: 1.5rem;
}
.product-list figure {
  background: #fff;
  padding: .8rem;
}
.product-list figure img {
  width: 100%;
  height: auto;
}
.product-list figure h4 {
  font-size: 1.5rem;
  line-height: 1.6;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  word-break: break-all;
  overflow: hidden;
}
.product-list figcaption {
  margin-top: 1rem;
}
.product-list figure p {
  margin-top: .4rem;
}
.product-list figure .price em {
  color: #ec5151;
  font-size: 1.8rem;
  display: block;
}
.product-list figure .remark em {
  font-size: 1.3rem;
  color: #888;
}
.product-list.small li {
  width: 100%;
}
.product-list.small li figure {
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: left;
  -webkit-box-align: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: start;
      align-items: flex-start;
}
.product-list.small li figure img {
  width: 9rem;
  height: auto;
}
.product-list.small li figure figcaption {
  -ms-flex: 1;
      flex: 1;
  -webkit-flex: 1;
  margin-left: .8rem;
}
.product-list.normal figure h4{
  /*height:4rem;*/
}
.product-list.big ul li {
  width: 100%;
}
.product-list.big li figure img {
  width: 100%;
}



